/* modules */
.hamburger {
  &--header {
    opacity: 1 !important;
    padding: 0 15px;
    height: 30px;
    width: 180px;
    position: relative;
    text-align: center;
    // display: none
    margin: 0 auto;
    z-index: 100;
    white-space: nowrap;
    .hamburger-box {
      width: 14px;
      height: 12px;
      top: 1px;
 }      // background-color: pink
    .hamburger-inner {
      &::before, &::after {
        background-color: $color-white;
        border-radius: 0;
        width: 14px;
        height: 2px; }
      &::before {
        top: 5px !important; }
      &::after {
        top: 10px !important; }
      background-color: $color-white;
      border-radius: 0;
      width: 14px;
      height: 2px;
      margin-top: 0;
      top: 0 !important; }
    &.is-active {
      .hamburger-inner {
        transform: translate3d(0, 5px, 0) rotate(45deg);
        background-color: $color-white;
        &::before, &::after {
          background-color: $color-white; }
        &::before {
          transform: rotate(-45deg) translate3d(-5.71429px, -2px, 0); }
        &::after {
          transform: translate3d(0, -10px, 0) rotate(-90deg); } }
      strong {
        color: $color-white; } }
    strong {
      color: $color-white;
      text-transform: uppercase;
      font-size: rem(14);
      font-family: $font-intro;
      margin-left: 7px;
 } } }      // position: absolute

.tab-pane {
  display: none;
  &.active {
    display: block; } }

// table
div.table {
  display: table;
  &>div {
    display: table-cell;
    vertical-align: top;
    &+div {
      padding-left: 15px; } } }

// Table
table.table {
