          bottom: 0; }
        &--one {
          .wheel--one {
            left: 117px;
            bottom: 19px; }
          .wheel--two {
            left: 494px;
            bottom: 21px; } }
        &--two {
          .wheel--one {
            left: 97px;
            bottom: 18px; }
          .wheel--two {
            left: 492px;
            bottom: 19px; } }
        &--three {
          .wheel--one {
            left: 106px;
            bottom: 21px; }
          .wheel--two {
            left: 524px;
            bottom: 23px; } }
        &--four {
          .wheel--one {
            left: 100px;
            bottom: 22px; }
          .wheel--two {
            left: 491px;
            bottom: 22px; } }
        &--five {
          .wheel--one {
            left: 150px;
            bottom: 19px; }
          .wheel--two {
            left: 491px;
            bottom: 21px; } } }

      &--animation {
        .tarif__main__auto__wrapper {
          opacity: 0; } } }
    &__title {
      font-size: rem(20);
      color: $color-red;
      text-transform: uppercase;
      font-family: $font-intro;
      line-height: 1.1;
      margin-bottom: 10px; } } }

/* taxi */
.taxi {
  padding-bottom: 0;
  text-align: center;
  &__wrapper {
    display: inline-table;
    &>div {
      display: table-cell;
      vertical-align: bottom;
      &+div {
        padding-left: 25px; } } }
  &__button {
    display: block;
    height: 120px;
    background: $color-red url('../img/tarif-button.png') 135px center no-repeat;
    width: 348px;
    font-size: rem(30);
    text-transform: uppercase;
    font-family: $font-intro;
    color: $color-white;
    position: relative;
    margin: 43px 0 20px 69px;
    position: relative;
    &::before {
      position: absolute;
      right: 0px;
      height: 0;
      width: 0;
      border-top: 120px solid transparent;
      border-right: 23px solid $color-white;
      content: '';
      transition: border-color 0.35s; }
    &>span {
      position: absolute;
      bottom: 13px;
      left: 23px;
      display: block;
      line-height: 1;
      white-space: nowrap; }
    &:hover, &:focus {
      color: $color-white;
      background-color: rgba($color-red,.8);
      &>i {
        &:nth-child(2) {
          border-color: rgba($color-red,.8); } } }
    &>i {
      position: absolute;
      top: 0;
      left: 0;
      &:nth-child(2) {
        border: 2px solid $color-red;
        height: 96px;
        width: 155px;
        transform: skewX(7deg);
        margin-top: -43px;
        margin-left: -63px;
        background: $color-white;
        transition: border 0.35s; }
      &:nth-child(3) {
        height: 51px;
        background-color: #ffce58;
        width: 75px;
        transform: skewX(7deg);
        margin-left: 55px;
        z-index: 5;
        margin-top: 0;
        .icon {
          position: absolute;
          left: 18px;
          top: 11px;
          transform: skewX(-7deg); } } } } }

